import React from 'react';
import ReactDOM from 'react-dom';
import './index.scss';
import { store } from './store/index';
import App from './App';
import { Provider } from 'react-redux';


  ReactDOM.render(

    <React.StrictMode>
      <Provider store={store}>
        <App />
      </Provider>
    </React.StrictMode>,

    document.getElementById('root')
  );






